@import './base.scss';
/**created by Rey on 2019.06.30***/
.offline{
  .main{
    padding: .2rem;
  }
  .search_box{
    width: 100%;
    height: .62rem;
    position: relative;
    margin-bottom: .2rem;
    input{
      display: block;
      width: 100%;
      height: 100%;
      border-radius: .31rem;
      padding-left: .2rem;
    }
    i{
      position: absolute;
      font-size: .3rem;
      right: .2rem;
      top: .02rem;
    }
  }
  .offline_group{
    .list{
      background: #fff;
      border-radius: .1rem;
      overflow: hidden;
      margin-bottom: .1rem;
      padding-bottom: .06rem;
      .top{
        height: .7rem;
        line-height: .7rem;
        margin-bottom: .06rem;
        padding-left: .26rem;
        border-bottom: .02rem solid #F2F2F2;
        .anem{
          font-size: .3rem;
        }
        .email{
          font-size: .24rem;
          margin-left: 1rem;
        }
      }
      .attr{
        &>div{
          width: 50%;
          padding: .1rem .26rem;
          &:first-child{
            border-right: .02rem solid #F2F2F2;
          }
        }
        &.attr1{
          border-bottom: .02rem solid #F2F2F2;
        }
        .label{
          font-size: .24rem;
          color: #999999;
          transform: scale(.8);
          transform-origin: left center;
        }
        .text{
          font-size: .24rem;
        }
      }
    }
  }
}
.withdrawal{
  .form{
    background: #fff;
    label{
      display: block;
      width: 100%;
      height: 1rem;
      line-height: 1rem;
      border-bottom: .02rem solid #F2F2F2;
      position: relative;
      padding-left: 2.5rem;
      overflow: hidden;
      margin: 0;
      span{
        position: absolute;
        left: .2rem;
        top: 0;
        bottom: 0;
        font-size: .24rem;
      }
      input{
        display: inline-block;
        height: 100%;
        border: none;
      }
    }
  }
  .btn_box{
    padding: 0 .2rem;
    margin-top: 2rem;
    .submit{
      display: block;
      width: 100%;
      height: .9rem;
      font-size: .36rem;
      border-radius: 0;
    }
  }
}
.income{
  .tabs{
    background: #fff;
    .tab{
      width: 50%;
      height: .9rem;
      line-height: .9rem;
      text-align: center;
      border: .02rem solid #fff;
      font-size: .3rem;
      &.active{
        color: $red;
        border-color: $red;
      }
    }
  }
  .tab_box{
    margin-top: .08rem;
    background: #fff;
    .box{
      display: none;
      &.active{
        display: block;
      }
    }
    .total{
      padding: .3rem .4rem;
      justify-content: space-between;
      margin-bottom: .3rem;
      .list{
        p{
          font-size: .24rem;
          margin-bottom: .1rem;
        }
        span{
          font-size: .36rem;
        }
      }
    }
    .group{
      li{
        height: 1rem;
        padding: 0 .4rem;
        border-top: .02rem solid $bg;
        justify-content: space-between;
        align-items: center;
        .lf{
          p{
            font-size: .24rem;
          }
          span{
            color: #959595;
          }
        }
        .price{
          font-size: .3rem;
        }
      }
    }
  }
}