@import './base.scss';
/**created by Rey on 2019.06.30***/
.user{
  &.main_page{
    padding-bottom: 1.2rem;
  }
  .top{
    background: #fff;
    padding: .3rem .4rem;
    .flex{
      align-items: center;
      justify-content: space-between;
      .head{
        flex-shrink: 0;
        width: 1.12rem;
        height: 1.12rem;
        margin-right: .3rem;
        .noHead{
          display: block;
          width: 100%;
          height: 100%;
          background: #CDCDCD;
          border-radius: 50%;
          font-size: .24rem;
          text-align: center;
          line-height: 1.12rem;
          color: #fff;
        }
        img{
          width: 100%;
          height: 100%;
          border-radius: 50%;
        }
      }
      .edit{
        flex-shrink: 0;
        width: .5rem;
        height: .5rem;
        i{
          font-size: .5rem;
          line-height: .5rem;
          color: $red;
        }
      }
      .mine{
        width: 100%;
        font-size: .3rem;
        .name{
          margin-bottom: .1rem;
        }
      }
    }
    .link{
      padding-left: .2rem;
      margin-top: .2rem;
      a{
        color: $red;
      }
    }
  }
  .menus{
    padding: .4rem 0;
    justify-content: space-around;
    a{
      display: block;
      width: 25%;
      text-align: center;
      position: relative;
      i{
        font-size: .5rem;
        line-height: .5rem;
      }
      p{
        font-size: .24rem;
        margin-top: .2rem;
      }
      b{
        position: absolute;
        top: -.2rem;
        right: .5rem;
        width: .4rem;
        height: .4rem;
        line-height: .4rem;
        text-align: center;
        font-size: .24rem;
        font-weight: normal;
        background: $red;
        border-radius: 50%;
        color: #fff;
        transform: scale(.8);
      }
    }
  }
  .links{
    background: #fff;
    .list{
      display: block;
      height: 1.2rem;
      line-height: 1.2rem;
      padding-left: .3rem;
      padding-right: .4rem;
      border-bottom: 1px solid #F2F2F2;
      &:last-child{
        border: none;
      }
      span{
        font-size: .34rem;
      }
      i{
        float: right;
        font-size: .5rem;
      }
    }
  }
  .center_box{
    width: 100%;
    margin-top: .4rem;
    padding: 0 .2rem;
    .B_center{
      display: block;
      width: 100%;
      height: 1rem;
      line-height: 1rem;
      padding: 0 .3rem;
      background: #fff;
      border-radius: .5rem;
      span{
        font-size: .4rem;
        color: $red;
      }
      i{
        float: right;
        font-size: .5rem;
      }
    }
  }
}
.userinfo{
  .main{
    padding: .2rem;
  }
  .top_tittle{
    font-size: .24rem;
    color: #999999;
    margin-bottom: .2rem;
  }
}
.changepassword{
  .form_box{
    &>label{
      position: relative;
      input{
        height: .8rem;
        font-size: .3rem;
      }
      i.iconfont{
        position: absolute;
        right: .2rem;
        top: .01rem;
        font-size: .4rem;
      }
    }
  }
}
.couponlist{
  .tabs{
    background: #fff;
    justify-content: space-around;
    .list{
      height: .9rem;
      line-height: .9rem;
      font-size: .34rem;
      color: #999999;
      border-bottom: .02rem solid transparent;
      &.active{
        color: $red;
        border-color: $red;
      }
    }
  }
  .coupon_group{
    padding: .6rem .2rem 0;
    .list{
      background: #fff;
      border-radius: .1rem;
      margin-bottom: .15rem;
      .coupon{
        padding: .2rem;
        align-items: center;
        .lf{
          width: 100%;
          border-right: 1px solid #E6E6E6;
          .name{
            color: $red;
            font-size: .4rem;
          }
          .info{
            color: #2A2A2A;
            font-size: .24rem;
            margin-top: .1rem;
            .time{
              margin-left: .15rem;
            }
          }
        }
        .rt{
          width: 1.8rem;
          flex-shrink: 0;
          padding-left: .1rem;
          .use{
            display: inline-block;
            width: 1.3rem;
            color: $red;
          }
          .iconfont{
            font-size: .3rem;
            color: #B3B3B3;
            display: inline-block;
            transition: transform .3s linear;
          }
        }
      }
      .detail{
        display: none;
        width: 100%;
        border-top: 1px solid #E6E6E6;
        padding: .4rem .3rem;
        color: #000020;
        .name{
          font-size: .24rem;
        }
        .info{
          font-size: .3rem;
          font-style: italic;
        }
      }
      &.expired{
        .coupon{
          .lf{
            .name{
              color: #999999;
            }
            .info{
              color: #999999;
            }
          }
          .rt{
            .use{
              color: #999999;
            }
          }
        }
      }
      &.show{
        .coupon{
          .rt{
            .iconfont{
              transform-origin: center center;
              transform: rotate(90deg);
            }
          }
        }
      }
    }
  }
}
.myorder{
  .main{
    padding: .2rem;
  }
  .search_box{
    width: 100%;
    height: .62rem;
    position: relative;
    margin-bottom: .2rem;
    input{
      display: block;
      width: 100%;
      height: 100%;
      border-radius: .31rem;
      padding-left: .2rem;
    }
    i{
      position: absolute;
      font-size: .3rem;
      right: .2rem;
      top: .02rem;
    }
  }
  .order_box{
    background: #fff;
    border-radius: .1rem;
    padding: .26rem .07rem;
    margin-bottom: .2rem;
    .order{
      padding: 0 .07rem .2rem;
      font-size: .24rem;
      border-bottom: 1px solid #F2F2F2;
      .status{
        float: right;
        color: $red;
      }
    }
    .group{
      li{
        justify-content: space-between;
        border-bottom: 1px solid #F2F2F2;
        padding: .2rem 0;
        img{
          flex-shrink: 0;
          width: 1.68rem;
          height: 1.68rem;
          margin-top: .1rem;
        }
        .info{
          width: 100%;
          padding: 0 .1rem;
          .name{
            font-size: .3rem;
            color: #4D4D4D;
          }
          .attr{
            font-size: .24rem;
            color: #999999;
            transform-origin: left center;
            transform: scale(.8);
            margin: .1rem 0;
          }
          .para{
            font-size: .24rem;
            .color{
              margin-right: .5rem;
            }
          }
        }
        .price{
          flex-shrink: 0;
          width: 1.2rem;
          text-align: right;
          margin-top: .1rem;
          p{
            color: $red;
            span{
              font-size: .24rem;
            }
          }
          &>span{
            font-size: .24rem;
            color: #858585;
          }
        }
      }
    }
    .bottom{
      text-align: right;
      p{
        color: #3D3D3D;
        margin-top: .1rem;
      }
      .btns{
        margin-top: .2rem;
      }
    }
  }
  .fixed_bottom{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    height: 1.2rem;
    background: #f2f2f2;
    justify-content: space-between;
    align-items: center;
    padding: 0 .3rem;
    .center{
      i{
        font-size: .6rem;
        color: $red;
      }
    }
    .page_box{
      .iconfont{
        display: inline-block;
        width: .5rem;
        height: .5rem;
        line-height: .5rem;
        text-align: center;
        font-size: .4rem;
        border: .02rem solid #333;
        border-radius: 50%;
        vertical-align: middle;
      }
      span{
        vertical-align: middle;
        margin: 0 .2rem;
      }
    }
  }
}
.logistics{
  .main{
    padding: .2rem;
  }
  .name{
    font-size: .36rem;
    font-weight: bold;
    color: #444444;
    height: .9rem;
    line-height: .9rem;
    background: #fff;
    border-radius: .1rem;
    padding-left: .2rem;
    margin-bottom: .2rem;
  }
  .shipped_box{
    background: #fff;
    border-radius: .1rem;
    overflow: hidden;
    .top{
      padding: .36rem .2rem;
      align-items: center;
      .img{
        flex-shrink: 0;
        width: 1rem;
        height: 1rem;
        img{
          width: 100%;
          height: 100%;
        }
      }
      .info{
        width: 100%;
        padding-left: .3rem;
        .shipped_name{
          font-size: .34rem;
          color: #444444;
        }
        .tel{
          font-size: .3rem;
          color: #444444;
          margin-top: .1rem;
        }
      }
    }
    .bottom{
      background: #FAFAFA;
      height: .88rem;
      padding: 0 .2rem;
      align-items: center;
      justify-content: space-between;
      p{
        font-size: .3rem;
        color: #444444;
      }
      i{
        font-size: .5rem;
        font-weight: bold;
      }
    }
  }
  .stores{
    height: .8rem;
    background: #fff;
    border-radius: .1rem;
    align-items: center;
    justify-content: space-between;
    padding: 0 .2rem;
    margin: .2rem 0;
    span{
      color: #444444;
      font-size: .3rem;
    }
    i{
      font-size: .5rem;
    }
  }
  .shipped_line{
    background: #fff;
    border-radius: .1rem;
    padding: .2rem .1rem;
    li{
      padding: .2rem 0;
      position: relative;
      &::before{
        content: '';
        position: absolute;
        z-index: 1;
        width: .02rem;
        height: 100%;
        top: 0;
        left: 1.05rem;
        background: #D2D2D2;
      }
      .time{
        text-align: right;
        flex-shrink: 0;
        width: .85rem;
        font-size: .24rem;
        color: #999;
        padding-right: .15rem;
        span{
          display: block;
          transform-origin: right center;
          transform: scale(.7);
        }
      }
      .circle{
        flex-shrink: 0;
        width: .42rem;
        height: .42rem;
        border-radius: 50%;
        background: #D2D2D2;
        margin-top: .1rem;
        position: relative;
        z-index: 2;
      }
      .info{
        padding-left: .2rem;
        span{
          font-size: .3rem;
          color: #999999;
          margin-bottom: .1rem;
        }
        p{
          font-size: .24rem;
          color: #999999;
        }
      }
      &.active{
        .circle{
          background: $red;
        }
        .info{
          span{
            color: #3C3C3C;
          }
          p{
            color: #3C3C3C;
          }
        }
      }
      &:first-child{
        &::before{
          height: 50%;
          top: auto;
          bottom: 0;
        }
      }
      &:last-child{
        &::before{
          height: 50%;
        }
        .circle{
          width: .12rem;
          height: .12rem;
          margin-left: .15rem;
          margin-top: .3rem;
        }
      }
    }
  }
}
.terminal{
  .info{
    padding: .5rem .2rem .3rem;
    .top{
      font-size: .3rem;
      color: #323232;
    }
    .text{
      margin-top: .5rem;
      font-size: .24rem;
    }
  }
  .link_group{
    background: #fff;
    a{
      height: 1.2rem;
      align-items: center;
      border-bottom: 1px solid #f2f2f2;
      padding-left: .3rem;
      padding-right: .4rem;
      .iconfont{
        flex-shrink: 0;
        width: .6rem;
        font-size: .5rem;
      }
      span{
        width: 100%;
        font-size: .3rem;
        padding-left: .2rem;
      }
    }
  }
}
.collection{
  .group{
    .list{
      padding: .2rem;
      border-bottom: .02rem solid #B8B8B8;
      .img_box{
        flex-shrink: 0;
        width: 1.7rem;
        height: 1.7rem;
      }
      .rt{
        width: 100%;
        padding: 0 .2rem;
        overflow: hidden;
        .top{
          position: relative;
          padding-right: .4rem;
          .iconhearts{
            position: absolute;
            font-size: .36rem;
            color: $red;
            top: -.1rem;
            right: -.1rem;
          }
        }
        .price{
          margin-top: .1rem;
          .old{
            color: #989898;
            margin-left: .25rem;
          }
        }
        .btn_box{
          text-align: right;
          margin-top: .1rem;
          .btn{
            height: .7rem;
            line-height: .7rem;
          }
        }
      }
    }
  }
}
.messagelist{
  .group{
    margin-top: .1rem;
    background: #fff;
    .list{
      padding: .3rem .2rem;
      border-bottom: .02rem solid #F3F3F3;
      a.flex{
        justify-content: space-between;
        align-items: center;
        .icon{
          flex-shrink: 0;
          width: .72rem;
          height: .72rem;
          line-height: .72rem;
          text-align: center;
          border-radius: 50%;
          background: #F2F2F2;
          i{
            font-size: .5rem;
            &.iconcoupon{
              color: $red;
            }
            &.iconorder{
              font-size: .4rem;
              color: #4EB4B3;
            }
            &.iconmsg{
              color: #E69C2E;
            }
          }
        }
      }
      &.active{
        .icon{
          position: relative;
          &::before{
            content: '';
            position: absolute;
            top: -.04rem;
            right: -.02rem;
            width: .2rem;
            height: .2rem;
            background: $red;
            border-radius: 50%;
          }
        }
      }
      .middle{
        overflow: hidden;
        width: 100%;
        padding: 0 .3rem;
        .name{
          font-size: .3rem;
        }
        .info{
          font-size: .24rem;
          color: #999999;
          margin-top: .1rem;
        }
      }
      .iconright{
        flex-shrink: 0;
        font-size: .4rem;
      }
    }
  }
}
.messagedetail{
  .group{
    margin-top: .1rem;
    background: #fff;
    .list{
      padding: .3rem .2rem;
      border-bottom: .02rem solid #F3F3F3;
      justify-content: space-between;
      .icon{
        flex-shrink: 0;
        width: .72rem;
        height: .72rem;
        line-height: .72rem;
        text-align: center;
        border-radius: 50%;
        background: #F2F2F2;
        i{
          font-size: .5rem;
          &.iconcoupon{
            color: $red;
          }
          &.iconorder{
            font-size: .4rem;
            color: #4EB4B3;
          }
          &.iconmsg{
            color: #E69C2E;
          }
        }
      }
      .rt{
        width: 100%;
        padding-left: .3rem;
        .top{
          padding-right: 1.2rem;
          overflow: hidden;
          position: relative;
          .name{
            font-size: .3rem;
          }
          .time{
            position: absolute;
            transform-origin: right center;
            right: 0;
            top: .05rem;
          }
        }
        .info{
          font-size: .24rem;
          color: #808080;
          margin-top: .1rem;
        }
      }
    }
  }
}
.videolist{
  .main{
    background: #fff;
  }
  .group{
    .list{
      margin-top: .1rem;
      &:before{
        padding-bottom: 50%;
      }
      .iconfont{
        position: absolute;
        z-index: 10;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: rgba($color: #000, $alpha: .5);
        color: #fff;
        font-size: 70px;
        text-align: center;
        &:before{
          position: absolute;
          width: 70px;
          height: 70px;
          line-height: 70px;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          margin: auto;
        }
      }
    }
  }
}