/**created by Rey on 2018.06.01***/
@import './base.scss';
.shopcart{
  .main{
    padding: .25rem .20rem;
  }
  .viewed{
    margin-top: .5rem;
    justify-content: space-between;
    font-size: .24rem;
    font-weight: bold;
  }
  .viewed_group{
    background: #fff;
    flex-wrap: wrap;
    padding: .2rem 0;
    margin-top: .2rem;
    li{
      flex-shrink: 0;
      width: 50%;
      padding: .1rem;
    }
  }
}
.shop_group{
  li{
    padding: .3rem .08rem;
    margin-bottom: .27rem;
    .lf{
      width: 1.68rem;
      flex-shrink: 0;
      padding-top: .2rem;
      .bottom{
        margin-top: .45rem;
        justify-content: space-between;
        i{
          color: $red;
          font-size: .32rem;
          font-weight: bold;
        }
      }
    }
    .rt{
      width: 100%;
      overflow: hidden;
      position: relative;
      padding-right: .8rem;
      padding-left: .2rem;
      .check_box{
        position: absolute;
        top: .06rem;
        right: .3rem;
      }
      .name{
        font-size: .3rem;
        color: #4D4D4D;
        margin-bottom: .16rem;
      }
      .info{
        font-size: .24rem;
        color: #999999;
        line-height: .36rem;
        transform: scale(.75);
        transform-origin: left center;
      }
      .attr{
        margin-bottom: .14rem;
        span{
          color: #666666;
          margin-right: .5rem;
          font-size: .24rem;
        }
      }
      .price{
        .now{
          font-size: .28rem;
          color: $red;
          font-weight: bold;
          margin-right: .1rem;
          i{
            display: inline-block;
            font-style: normal;
            font-size: .24rem;
            transform: scale(.75);
          }
        }
        .old{
          font-size: .24rem;
          color: #ccc;
          text-decoration: line-through;
          margin-right: .1rem;
          i{
            font-style: normal;
          }
        }
        .integral{
          font-size: .24rem;
          color: #666;
        }
      }
      .num_box{
        float: right;
        position: relative;
        width: 1.4rem;
        height: .36rem;
        padding: 0 .4rem;
        margin-top: .14rem;
        margin-right: -.4rem;
        span{
          position: absolute;
          left: 0;
          top: 0;
          width: .3rem;
          height: .3rem;
          line-height: .3rem;
          text-align: center;
          background: $red;
          color: #fff;
          border-radius: 50%;
          font-size: .24rem;
        }
        .add{
          left: auto;
          right: 0;
        }
        input{
          width: 100%;
          height: .36rem;
          border: none;
          border-bottom: 1px solid #CBCBCB;
          vertical-align: top;
          text-align: center;
          font-size: .28rem;
        }
      }
    }
  }
}
.order{
  .main{
    padding: .25rem .20rem;
  }
  .addr{
    align-items: center;
    padding: .2rem;
    margin-bottom: .4rem;
    .lf{
      width: 100%;
      .top{
        .name{
          font-size: .3rem;
          font-weight: bold;
        }
        .phone{
          font-weight: bold;
          font-size: .24rem;
          margin-left: .5rem;
        }
      }
      .detail{
        font-size: .24rem;
        color: #666666;
        margin-top: .18rem;
        line-height: .3rem;
      }
    }
    .rt{
      width: .6rem;
      i{
        font-size: .4rem;
      }
    }
  }
  .shipping{
    align-items: center;
    padding: .2rem;
    margin-bottom: .1rem;
    justify-content: space-between;
    .lf{
      flex-shrink: 0
    }
    .mid{
      width: 100%;
      padding: 0 .3rem;
      line-height: .4rem;
    }
    .rt{
      flex-shrink: 0;
      width: .6rem;
      i{
        font-size: .4rem;
      }
    }
  }
  .shipping_free{
    font-size: .24rem;
    margin-bottom: .3rem;
    span{
      color: $red;
    }
  }
  .time_box{
    padding: .2rem .1rem;
    align-items: center;
    span{
      width: 33.33%;
    }
    .name{
      font-size: .3rem;
    }
    .time, .pos{
      font-size: .24rem;
      color: #929292;
    }
  }
  .insurance{
    padding: .2rem .1rem;
    align-items: center;
    justify-content: space-between;
    margin: .2rem 0;
    .name{
      font-size: .3rem;
    }
  }
  .coupon_box{
    padding: .2rem .1rem;
    align-items: center;
    justify-content: space-between;
    .name{
      flex-shrink: 0;
      font-size: .3rem;
    }
    .rt{
      flex-shrink: 0;
      width: .6rem;
      i{
        font-size: .4rem;
      }
    }
    .mid{
      width: 100%;
      text-align: right;
      padding-right: .3rem;
      font-size: .3rem;
      color: #8C8C8C;
    }
  }
  .coupon_text{
    font-size: .3rem;
    color: #020202;
    span{
      color: $red;
    }
  }
  .summary{
    padding: 0 .1rem;
    margin-top: .3rem;
    .top{
      font-size: .3rem;
      line-height: .8rem;
    }
    ul{
      border-top: 1px solid #EBEBEB;
      border-bottom: 1px solid #EBEBEB;
      li{
        justify-content: space-between;
        font-size: .26rem;
        line-height: .46rem;
      }
    }
    .bottom{
      padding-bottom: .2rem;
      .total{
        justify-content: space-between;
        font-size: .3rem;
        font-weight: bold;
        margin: .1rem;
      }
      .reward{
        font-size: .24rem;
        text-align: right;
        span{
          color: $red;
        }
      }
    }
  }
}
.topay{
  .main{
    padding: .25rem .20rem;
  }
  .select_box{
    label{
      width: 100%;
      justify-content: space-between;
      align-items: center;
      height: 1rem;
      padding: 0 .2rem;
      border-bottom: 1px solid #E6E6E6;
      &:last-child{
        border: none;
      }
      img{
        height: .7rem;
        vertical-align: middle
      }
      p{
        img{
          height: .5rem;
        }
        i{
          font-size: .24rem;
        }
        span{
          vertical-align: middle;
          font-size: .3rem;
          color: #575757;
          margin-left: .2rem;
        }
      }
      .check_box{
        width: .62rem;
        height: .62rem;
        input:checked+span:after{
          left: .22rem;
          top: .1rem;
          width: .18rem;
          height: .28rem;
        }
      }
    }
  }
  .fixed_shop{
    height: 1.2rem;
    .lf{
      .num{
        position: absolute;
        left: .2rem;
        bottom: 1.4rem;
        text-align: left;
      }
      .price{
        font-size: .4rem;
        margin-top: .16rem;
      }
    }
    .btn{
      height: 1rem;
      border-radius: .5rem;
    }
  }
}
.coupon{
  .main{
    padding: .2rem;
  }
  .coupon_group{
    &>label{
      background: #fff;
      border-radius: .1rem;
      margin-bottom: .1rem;
      padding: .26rem .2rem;
      justify-content: space-between;
      align-items: center;
      .lf{
        p{
          font-size: .4rem;
          color: $red;
          margin-bottom: .2rem;
        }
        .info{
          font-size: .24rem;
          .over{
            margin-right: .2rem;
          }
        }
      }
      .check_box{
        width: .62rem;
        height: .62rem;
        input:checked+span:after{
          left: .22rem;
          top: .1rem;
          width: .18rem;
          height: .28rem;
        }
      }
      &.disabled{
        .lf{
          p, .info{
            color: #999999;
          }
        }
      }
    }
  }
}
.couponadd{
  .main{
    padding: .37rem .2rem;
  }
  .coupon{
    background: #fff;
    border-radius: .1rem;
    .top{
      color: $red;
      font-size: .4rem;
      height: 1.06rem;
      line-height: 1.06rem;
      padding: 0 .2rem;
      border-bottom: 1px solid #E6E6E6;
    }
    .bottom{
      p{
        line-height: .6rem;
        font-size: .24rem;
        color: #2A2A2A;
        padding-left: .2rem;
        span{
          min-height: 1rem;
        }
      }
    }
  }
}
.paystatus{
  .main{
    text-align: center;
    padding: .3rem .2rem;
  }
  .top{
    .icon{
      width: 2.6rem;
      height: 2.6rem;
      line-height: 2.6rem;
      border-radius: 50%;
      background: #fff;
      text-align: center;
      margin: 0 auto;
      i{
        font-size: 1.8rem;
      }
    }
    .status{
      font-size: .36rem;
      margin-top: .5rem;
    }
    .text{
      font-size: .24rem;
      margin-top: .25rem;
    }
    .order{
      font-size: .3rem;
      margin-top: .9rem;
    }
    .res{
      margin-top: .75rem;
      background: #fff;
      border-radius: .1rem;
      padding: .2rem;
      color: #454545;
      font-size: .24rem;
      text-align: left;
      min-height: 2rem;
    }
    &.paysuccess{
      .icon{
        i{
          color: #70BE1C;
        }
      }
      .status{
        color: #70BE1C;
      }
    }
    &.submitted{
      .icon{
        i{
          color: #F69D1E;
        }
      }
      .status{
        color: #F69D1E;
      }
    }
    &.phone{
      .icon{
        i{
          font-size: .8rem;
          color: $red;
        }
      }
      .status{
        color: $red;
      }
      .tel{
        margin-top: .3rem;
      }
    }
    &.error{
      .icon{
        i{
          color: $red;
        }
      }
      .status{
        color: $red;
      }
      .btn{
        margin-top: .3rem;
      }
      .res{
        .name{
          margin-bottom: .2rem;
        }
      }
    }
  }
  .btns{
    margin-top: 1.5rem;
    justify-content: space-between;
    .btn{
      padding: 0;
      width: 2.2rem;
    }
  }
}
.refund{
  .product_box{
    background: #fff;
    .shop_group{
      width: 100%;
      li{
        border-radius: 0;
        box-shadow: none;
        border-bottom: .02rem dashed #ccc;
        padding: .15rem .2rem;
        &:last-child{
          border: none;
        }
        .rt{
          padding-right: 0;
        }
      }
    }
  }
  .detail{
    background: #fff;
    margin-top: .08rem;
    padding: .1rem .2rem;
    label{
      display: block;
      width: 100%;
      .label{
        font-size: .3rem;
      }
    }
    .goods{
      height: .7rem;
      line-height: .6rem;
      border-bottom: .02rem solid #ccc;
      span{
        float: left;
        width: 2.7rem;
      }
      .input{
        margin-left: 2.7rem;
        input{
          width: 100%;
          height: .6rem;
          border: none;
        }
      }
    }
    .refund{
      .label{
        display: block;
        line-height: .6rem;
      }
      textarea{
        width: 100%;
        height: 1.9rem;
        border-color: #ccc;
      }
    }
    .amount{
      line-height: .6rem;
      font-size: .3rem;
    }
  }
  .photos_box{
    background: #fff;
    padding: .2rem;
    margin-top: .08rem;
    .name{
      font-size: .3rem;
      margin-bottom: .2rem;
    }
    .img_group{
      justify-content: space-between;
    }
  }
  .btn_box{
    margin-top: .5rem;
    padding: 0 .2rem;
    .submit{
      display: block;
      width: 100%;
      border-radius: 0;
      font-size: .36rem;
      height: .9rem;
      line-height: .9rem;
    }
  }
}