@import './base.scss';
/**created by Rey on 2019.07.07***/
$reds: #DA251D;
.audit{
  background: #fff;
  .main{
    padding: .2rem;
  }
  .top_box{
    .logo{
      font-size: .32rem;
      font-weight: bold;
      color: $reds;
      width: 5.8rem;
      margin: 0 auto;
    }
    h1{
      font-size: .3rem;
      font-weight: bold;
      color: $reds;
      margin: .3rem 0;
      text-align: center;
    }
    .info{
      font-size: .24rem;
    }
    .name_box{
      margin-top: .25rem;
      .box{
        span{
          color: $red;
        }
        input{
          width: 1.7rem;
          border: none;
          border-bottom: .02rem solid #505050;
          margin-left: .1rem;
        }
      }
      .text{
        font-size: .24rem;
        color: #505050;
        margin-top: .15rem;
      }
    }
  }
  .form{
    .list_name{
      font-size: .3rem;
      font-weight: bold;
      margin-top: .4rem;
      margin-bottom: .3rem;
      color: $reds;
    }
    label{
      display: block;
      width: 100%;
      margin-bottom: .3rem;
      p{
        font-size: .24rem;
        color: #505050;
        margin-bottom: .1rem;
      }
      input, textarea{
        display: block;
        width: 100%;
        height: .74rem;
        border: .02rem solid #999999;
      }
      textarea{
        height: 2.7rem;
      }
    }
    .add_info{
      align-items: center;
      p{
        width: 100%;
        font-size: .24rem;
        line-height: .4rem;
        color: $reds;
      }
      i{
        flex-shrink: 0;
        font-size: .6rem;
        line-height: .6rem;
        color: $reds;
      }
    }
    &>p.info{
      font-size: .24rem;
      color: $reds;
      margin-bottom: .3rem;
      margin-top: -.2rem;
    }
    .pictures_box{
      margin-top: .5rem;
      .label{
        font-size: .3rem;
        font-weight: bold;
        margin-bottom: .3rem;
      }
      .img_group{
        justify-content: space-between;
        .img_upload{
          width: 1.68rem;
        }
      }
    }
    .submit{
      margin-top: .5rem;
      width: 100%;
      height: .9rem;
      font-size: .36rem;
      border-radius: 0;
    }
  }
}
.auditstatus{
  background: #fff;
  .main{
    min-height: calc(100vh - 1.8rem);
  }
  .status{
    padding: .5rem 0 .5rem .7rem;
    background: $bg;
    i{
      font-size: 1rem;
      line-height: 1rem;
      vertical-align: middle;
      color: $red;
    }
    span{
      font-size: .36rem;
      margin-left: .3rem;
      vertical-align: middle;
    }
  }
  .info_line{
    background: #fff;
    border-radius: .1rem;
    padding: .3rem;
    margin-top: .2rem;
    li{
      padding: .2rem 0;
      position: relative;
      &::before{
        content: '';
        position: absolute;
        z-index: 1;
        width: .02rem;
        height: 100%;
        top: 0;
        left: 1.05rem;
        background: #D2D2D2;
      }
      .time{
        text-align: right;
        flex-shrink: 0;
        width: .85rem;
        font-size: .24rem;
        color: #999;
        padding-right: .15rem;
        span{
          display: block;
          transform-origin: right center;
          transform: scale(.7);
        }
      }
      .circle{
        flex-shrink: 0;
        width: .42rem;
        height: .42rem;
        line-height: .42rem;
        border-radius: 50%;
        background: #9A9A9A;
        margin-top: .1rem;
        position: relative;
        z-index: 2;
        font-size: .24rem;
        color: #fff;
        text-align: center;
      }
      .info{
        padding-left: .2rem;
        padding-top: .15rem;
        p{
          font-size: .24rem;
          color: #999999;
        }
      }
      &:first-child{
        .circle{
          background: $red;
          &:before{
            content: "\e60e";
          }
        }
        &::before{
          height: 50%;
          top: auto;
          bottom: 0;
        }
      }
      &:last-child{
        &::before{
          height: 50%;
        }
        .circle{
          width: .12rem;
          height: .12rem;
          margin-left: .15rem;
          margin-top: .3rem;
          &::before{
            content: '';
          }
        }
      }
    }
  }
  .view_box{
    padding: 0 .2rem;
    margin-top: 2rem;
    .view{
      display: block;
      width: 100%;
      height: .8rem;
      border-radius: .1rem;
    }
  }
}