@import './base.scss';
/**created by Rey on 2019.06.30***/
.top_title{
  font-size: .24rem;
  color: #999999;
  line-height: .3rem;
}
.addresslist{
  .main{
    padding: .2rem;
  }
  .addr_group{
    margin-top: .7rem;
    li{
      background: #fff;
      border-radius: .1rem;
      padding: .3rem .2rem;
      margin-bottom: .27rem;
      position: relative;
      .top{
        line-height: .6rem;
        .phone{
          margin-left: .5rem;
        }
        a{
          float: right;
          color: $red;
          i{
            line-height: .6rem;
            font-size: .6rem;
          }
        }
      }
      .info{
        line-height: .4rem;
        margin: .3rem 0;
      }
      .default{
        margin-top: .3rem;
        i{
          font-size: .24rem;
          color: $red;
        }
        span{
          color: #999999;
          font-size: .24rem;
        }
      }
      .icondel{
        position: absolute;
        right: .2rem;
        bottom: .04rem;
        font-size: .44rem;
        color: $red;
      }
    }
  }
  .add_addr{
    margin-top: 1rem;
    display: block;
    width: 100%;
    height: 1rem;
    line-height: 1rem;
    text-align: center;
    background: #fff;
    border-radius: .1rem;
    color: $red;
    font-size: .3rem;
    font-weight: bold;
    i{
      font-size: .7rem;
      vertical-align: middle;
    }
  }
}
.addressdetail{
  .main{
    padding: .2rem;
  }
  .detail{
    background: #fff;
    border-radius: .1rem;
    padding: .15rem;
    margin-top: .2rem;
    input:disabled, textarea:disabled{
      background: #fff;
    }
    .country{
      margin-top: .6rem;
    }
    .default{
      font-size: .3rem;
      .check_box{
        width: .66rem;
        height: .66rem;
        vertical-align: middle;
        margin-right: .2rem;
        input:checked+span:after{
          width: .2rem;
          height: .4rem;
          left: .22rem;
        }
      }
    }
  }
  .save{
    width: 100%;
    margin-top: .6rem;
    border-radius: .1rem;
  }
}
.addressself{
  .search_box{
    margin-bottom: -.4rem;
    input{
      display: block;
      width: 100%;
      height: .57rem;
      font-size: .24rem;
      border-radius: .06rem;
      padding-left: .2rem;
    }
    span{
      width: 1rem;
      flex-shrink: 0;
      text-align: center;
      font-size: .24rem;
      color: #4C4C4C;
      line-height: .57rem;
    }
  }
}