@import './base.scss';
/**created by Rey on 2019.07.01***/
.categories{
  .main{
    padding: .2rem;
  }
  .menu{
    position: fixed;
    left: 0;
    top: 1rem;
    bottom: 1.2rem;
    width: 2rem;
    background: #fff;
    overflow-y: auto;
    li{
      width: 100%;
      height: 1.1rem;
      line-height: 1.1rem;
      text-align: center;
      &.active{
        background: $bg;
      }
    }
  }
  .detail{
    width: 100%;
    padding-left: 2rem;
    .banner{
      &::before{
        padding-bottom: 60%;
      }
    }
    h2{
      font-size: .24rem;
      font-weight: bold;
      margin: .2rem 0 .4rem;
    }
    .group{
      justify-content: space-between;
      flex-wrap: wrap;
      li{
        width: 1.6rem;
        margin-bottom: .4rem;
        .title{
          font-size: .24rem;
          margin-top: .2rem;
          text-align: center;
          transform: scale(.8);
        }
      }
    }
  }
}
.productlist{
  min-height: calc(100vh - 2.2rem);
  background: #fff;
  .main{
    padding-top: .98rem;
  }
  .filter_box{
    position: fixed;
    z-index: 9999;
    top: 1rem;
    left: 0;
    right: 0;
    padding: .05rem 0;
    border-bottom: .02rem solid #E6E6E6;
    background: #fff;
    .list{
      width: 25%;
      height: .8rem;
      text-align: center;
      line-height: .8rem;
      border-right: .02rem solid #E6E6E6;
      &:last-child{
        border: none;
      }
      span{
        font-size: .3rem;
        color: #303030;
        vertical-align: middle;
        margin-right: .1rem;
      }
      i{
        display: inline-block;
        font-size: .36rem;
        color: #333;
        vertical-align: middle;
        transform: rotate(0deg);
        transform-origin: center;
        transition: transform .2s linear;
      }
      &.screen{
        font-weight: bold;
      }
      &.active{
        i{
          transform: rotate(-180deg);
        }
      }
    }
  }
  .product_group{
    padding: 0;
    li{
      float: left;
      width: 50%;
      padding: .2rem .2rem .4rem;
      border-bottom: 1px solid #E6E6E6;
      border-right: 1px solid #fff;
      &:nth-child(2n+1){
        border-right: 1px solid #E6E6E6;
      }
      .img_box{
        display: block;
        width: 100%;
        .type{
          position: absolute;
          z-index: 2;
          left: 0;
          top: 0;
          font-size: .24rem;
          padding: .02rem .2rem;
          min-width: .6rem;
          text-align: center;
          background: $red;
          color: #fff;
          border-bottom-right-radius: .08rem;
        }
        .favorite{
          position: absolute;
          z-index: 2;
          top: .05rem;
          right: .1rem;
          font-size: .4rem;
          line-height: .4rem;
          color: $red;
        }
      }
      .info_box{
        margin-top: .4rem;
        min-height: 2.5rem;
        transform-style: preserve-3d;
        transform-origin: 50% 50% 0;
        transform: rotateY(0deg);
        transition: transform .3s linear;
        .info{
          background: #fff;
          transform: translateZ(1px);
          overflow: hidden;
          .price{
            line-height: .4rem;
            font-size: .24rem;
            .now{
              color: #4D4D4D;
            }
            .old{
              color: #999999;
              margin-left: .15rem;
              text-decoration: line-through;
            }
            .discount{
              float: right;
              height: .4rem;
              background: #5F89C6;
              color: #fff;
              font-size: .24rem;
              padding: 0 .15rem;
              border-radius: .08rem;
            }
          }
          .order{
            color: #999999;
          }
          .flash{
            color: $red;
          }
          .consultation{
            width: 100%;
            height: .7rem;
            line-height: .7rem;
            margin-top: .2rem;
            i{
              margin-left: .2rem;
            }
          }
        }
        .locl{
          position: absolute;
          z-index: 1;
          left: 0;
          top: 0;
          right: 0;
          bottom: 0;
          background: #fff;
          transform: translateZ(-1px);
          transform: rotateY(180deg);
          overflow: hidden;
          padding: 0 .04rem;
          .store{
            i{
              font-size: .3rem;
              color: $red;
            }
          }
          .contact{
            width: 99%;
            padding: 0;
            height: .7rem;
            line-height: .7rem;
            margin-top: .2rem;
            border-radius: .35rem;
            a{
              float: left;
              width: 50%;
              color: #fff;
              i{
                font-size: .24rem;
              }
              &:first-child{
                border-right: .01rem solid #eee;
                i{
                  font-size: .55rem;
                }
              }
            }
          }
        }
        &.showLocal{
          transform: rotateY(180deg);
        }
      }
      .info_box_B{
        padding-top: .2rem;
        .listing{
          height: .3rem;
        }
        .flash{
          width: 100%;
          height: .3rem;
          color: $red;
        }
        .see_detail{
          width: 100%;
          height: .7rem;
          line-height: .7rem;
          margin-top: .1rem;
        }
      }
    }
  }
}
.productdetail{
  padding-bottom: 1rem !important;
  .product_banner{
    .swiper-container{
      padding-bottom: 100%;
    }
    .swiper-pagination{
      text-align: left;
      padding-left: .2rem;
      .swiper-pagination-switch{
        opacity: 1;
        &.swiper-active-switch{
          background: #333;
        }
      }
    }
  }
  .info_box{
    background: #fff;
    padding: .3rem .2rem .2rem;
    .top{
      line-height: .4rem;
      .now{
        float: left;
        font-size: .3rem;
        font-weight: bold;
      }
      .old{
        float: left;
        font-size: .3rem;
        color: #999999;
        margin-left: .25rem;
      }
      .time{
        float: left;
        font-size: .24rem;
        color: $red;
        margin-left: .4rem;
      }
      .sale{
        float: right;
        margin-right: .25rem;
        font-size: .24rem;
        background:  #448ACA;
        color: #fff;
        height: .4rem;
        line-height: .4rem;
        padding: 0 .1rem;
        border-radius: .1rem;
      }
    }
    .bottom{
      margin-top: .1rem;
      .orders, .integral{
        margin-left: .3rem;
        font-size: .24rem;
        color: #999999;
      }
    }
  }
  .product_box{
    margin-top: .08rem;
    background: #fff;
    .name{
      padding: .2rem;
      font-size: .3rem;
    }
    .description{
      padding: 0 .2rem;
      font-size: .24rem;
      line-height: .3rem;
    }
    .gift{
      margin-top: .2rem;
      background: $red;
      padding: .1rem .2rem;
      align-items: center;
      img{
        flex-shrink: 0;
        width: 1rem;
        height: 1rem;
      }
      .rt{
        width: 100%;
        padding-left: .1rem;
        p{
          color: #fff;
          font-size: .24rem;
          margin: .1rem 0;
        }
      }
    }
    .bottom{
      height: 1rem;
      justify-content: space-between;
      align-items: center;
      padding: 0 .2rem;
      .delivery{
        font-size: .3rem;
      }
      .post{
        font-size: .24rem;
        i{
          font-size: .3rem;
          line-height: .3rem;
          font-weight: bold;
        }
      }
    }
  }
  .attrs{
    margin-top: .08rem;
    padding: .3rem .2rem;
    background: #fff;
    .group{
      margin-bottom: .35rem;
      &:last-child{
        margin: 0;
      }
      p{
        font-size: .24rem;
        color: #484848;
      }
      .list{
        span{
          float: left;
          margin-right: .35rem;
          height: .5rem;
          line-height: .5rem;
          padding: 0 .2rem;
          min-width: 1rem;
          text-align: center;
          border: .002rem solid #626262;
          margin-top: .1rem;
          &.active{
            border-color: $red;
            color: $red;
          }
        }
      }
    }
  }
  .feed_back{
    margin-top: .08rem;
    background: #fff;
    padding: .2rem;
    .top{
      justify-content: space-between;
      align-items: center;
      span{
        font-size: .3rem;
        color: #4D4D4D;
      }
      a{
        font-size: .24rem;
        i{
          font-size: .3rem;
          font-weight: bold;
        }
      }
    }
    .sort{
      margin-top: -.15rem;
    }
    .back_box{
      margin-top: .5rem;
      img{
        flex-shrink: 0;
        width: .7rem;
        height: .7rem;
      }
      .rt{
        width: 100%;
        padding-left: .1rem;
        .top{
          .lf{
            float: left;
            font-size: .24rem;
            color: #4D4D4D;
          }
          .sort{
            float: right;
          }
        }
        .info{
          font-size: .24rem;
          color: #4D4D4D;
          margin: .25rem 0;
        }
        .attrs_show{
          span{
            color: #B0B0B0;
            font-size: .24rem;
            margin-right: .5rem;
          }
        }
      }
    }
  }
  .info_box_B{
    background: #fff;
    margin-top: .08rem;
    .top{
      padding: .2rem;
      p{
        line-height: .4rem;
        span{
          width: 20%;
          font-size: .24rem;
        }
        .label{
          width: 40%;
        }
      }
    }
    .name{
      font-size: .3rem;
      margin-bottom: .2rem;
      padding: 0 .2rem;
    }
    .description{
      padding: 0 .2rem;
      font-size: .24rem;
      line-height: .3rem;
    }
    .attrs{
      p{
        line-height: .4rem;
        span{
          margin-right: .3rem;
        }
        .attr{
          display: inline-block;
          width: 1rem;
          margin-right: 0;
        }
      }
    }
  }
  .stors{
    background: #fff;
    margin-top: .08rem;
    .top_name{
      font-size: .3rem;
      font-weight: bold;
      color: #484848;
      padding: .3rem .2rem;
      a{
        float: right;
        font-weight: normal;
        margin-top: -.1rem;
      }
    }
    .swiper-container{
      height: 3.5rem;
      padding-bottom: .3rem;
    }
    .swiper-wrapper{
      position: relative;
      height: auto;
    }
    .swiper-slide{
      height: auto;
      width: 6rem;
      padding: 0 .2rem;
    }
  }
  .commodity{
    margin-top: .08rem;
    background: #fff;
    padding-bottom: .2rem;
    .name{
      padding: .2rem;
      font-size: .3rem;
      color: #4C4C4C;
      margin-bottom: .3rem;
    }
  }
  .fixed_buy{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 9999;
    height: 1rem;
    background: #fff;
    align-items: center;
    border: .02rem solid $red;
    text-align: center;
    .operats{
      width: 60%;
      height: 100%;
      padding: .04rem 0;
      .opera{
        width: 100%;
        height: 100%;
        line-height: .9rem;
        border-right: .02rem solid $red;
        &:last-child{
          border: none;
        }
        i{
          font-size: .7rem;
          color: $red;
        }
      }
    }
    .buy{
      width: 40%;
      height: 100%;
      line-height: .96rem;
      background: $red;
      color: #fff;
      font-size: .45rem;
    }
  }
}
.store_box{
  border: .02rem solid #ccc;
  box-shadow: .02rem .02rem .06rem 0 #eee;
  .store{
    background: #F2F2F2;
    .img{
      flex-shrink: 0;
      width: 2.3rem;
      height: 2.3rem;
    }
    .rt{
      width: 100%;
      padding: .1rem;
      flex-wrap: wrap;
      .top{
        width: 100%;
        .name, .contact{
          font-size: .27rem;
          color: #484848;
        }
        .contact{
          margin-top: .06rem;
        }
      }
      .bottom{
        width: 100%;
        align-items: center;
        align-self: flex-end;
        i{
          flex-shrink: 0;
          font-size: .4rem;
          color: $red;
        }
        .title_2{
          width: 100%;
          padding-left: .1rem;
          font-size: .24rem;
        }
      }
    }
  }
  .contacts{
    background: #fff;
    border-top: .02rem solid #ccc;
    a{
      width: 50%;
      height: .7rem;
      line-height: .7rem;
      text-align: center;
      i{
        font-size: .7rem;
        color: $red;
        font-weight: bold;
      }
      &.tel{
        border-left: .02rem solid #ccc;
        i{
          font-size: .3rem;
        }
      }
    }
  }
}
.feedback{
  .main{
    min-height: calc(100vh - 1.8rem);
    background: #fff;
  }
  .stars_group{
    padding: .3rem;
    border-bottom: .02rem solid #E6E6E6;
    .list{
      display: inline-block;
      height: .66rem;
      line-height: .64rem;
      border-radius: .33rem;
      padding: 0 .3rem;
      font-size: .24rem;
      border: .02rem solid #C3C3C3;
      color: #C3C3C3;
      margin-right: .2rem;
      margin-bottom: .1rem;
      &.active{
        color: $red;
        border-color: $red;
      }
      span{
        margin-right: .1rem;
      }
    }
  }
  .feed_group{
    .back_box{
      padding: .2rem;
      border-bottom: .02rem solid #E6E6E6;
      .head{
        flex-shrink: 0;
        width: .7rem;
        height: .7rem;
      }
      .rt{
        width: 100%;
        padding-left: .1rem;
        .top{
          .lf{
            float: left;
            font-size: .24rem;
            color: #4D4D4D;
          }
          .sort{
            float: right;
          }
        }
        .img_group{
          width: 100%;
          margin: .1rem 0;
          justify-content: space-between;
          img{
            width: 1.4rem;
            height: 1.4rem;
          }
        }
        .info{
          font-size: .24rem;
          color: #4D4D4D;
          margin: .25rem 0;
        }
        .attrs_show{
          span{
            color: #B0B0B0;
            font-size: .24rem;
            margin-right: .5rem;
          }
        }
      }
    }
  }
  .fixed_bottom{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    height: 1rem;
    background: #f2f2f2;
    justify-content: flex-end;
    align-items: center;
    padding: 0 .3rem;
    .center{
      i{
        font-size: .6rem;
        color: $red;
      }
    }
    .page_box{
      .iconfont{
        display: inline-block;
        width: .5rem;
        height: .5rem;
        line-height: .5rem;
        text-align: center;
        font-size: .4rem;
        border: .02rem solid #333;
        border-radius: 50%;
        vertical-align: middle;
      }
      span{
        vertical-align: middle;
        margin: 0 .2rem;
      }
    }
  }
}
.stores{
  .store_group{
    padding: .4rem;
    .store_box{
      margin-bottom: .3rem;
    }
  }
}