@import './base.scss';
/**created by Rey on 2019.07.01***/
// 登录注册
.login{
  &.main_page{
    padding-bottom: 0;
  }
  .fixed_top{
    background: #fff;
    span, i{
      color: #333;
    }
  }
  .tabs{
    background: #fff;
    height: .8rem;
    margin-bottom: .2rem;
    align-items: center;
    justify-content: space-around;
    .tab{
      width: 45%;
      height: 100%;
      line-height: .8rem;
      text-align: center;
      font-size: .3rem;
      border-bottom: .03rem solid #fff;
      &.active{
        color: $red;
        border-color: $red;
      }
    }
  }
  .page_box{
    height: calc(100vh - 1.8rem);
    background: #fff;
    .swiper-slide{
      overflow-y: auto;
    }
  }
  .form_box{
    padding: .6rem .3rem;
    &>label, .phone_box{
      position: relative;
      input{
        height: .8rem;
        font-size: .3rem;
      }
      &>i.iconfont{
        position: absolute;
        right: .2rem;
        top: .01rem;
        font-size: .4rem;
      }
      .select{
        height: .8rem;
        line-height: .6rem;
        margin-top: 0;
        i{
          top: .2rem;
        }
      }
    }
    .forgot{
      text-align: right;
      a{
        color: $red;
        font-size: .24rem;
      }
    }
  }
  .bottom_box{
    padding: .3rem;
    text-align: center;
    .login, .create{
      display: block;
      width: 100%;
      height: 1rem;
      font-size: .36rem;
    }
    .others{
      margin-top: .85rem;
      .other{
        display: inline-block;
        width: 1rem;
        height: 1rem;
        margin: 0 .4rem;
        img{
          width: 100%;
          height: 100%;
        }
      }
    }
    .create{
      margin-top: -.5rem;
    }
    .text{
      font-size: .24rem;
      margin: .5rem 0 -.3rem;
    }
    .to_login, .to_acoount{
      margin-top: .5rem;
      font-size: .24rem;
      color: $red;
      font-weight: bold;
    }
  }
}
.recovery{
  .main{
    margin-top: .2rem;
    min-height: calc(100vh - 1rem);
    background: #fff;
  }
  .submit_box{
    margin-top: .2rem;
    padding: 0 .3rem;
    .submit{
      width: 100%;
      height: 1rem;
      font-size: .36rem;
    }
  }
}